@import url(https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400);
body {
  background-image: url(/static/img/background.f05e006.jpg);
  background-size: cover;
  margin: 0;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 25px;
  font-weight: 300;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
header {
  height: 15vh;
}
header img {
    margin-left: 60px;
}
.quiz {
  text-align: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  max-height: 65vh;
  padding: 0 5%;
}
footer {
  background-image: url(/static/img/footer.3e40163.png);
  background-size:100%;
  background-position: bottom;
  background-repeat: no-repeat;
  height: 20vh;
}


